import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';
import { SIZES } from '../../constant/props';

class Navbar extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			advanced,
			size,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'navbar',
			'clearfix',
			{
				['advanced']:advanced,
				['navbar--' + size]:size,
			},
		className);

		return (
			<div
				{...others}
				className={klassName}
			>
				{children}
			</div>
		);
	}
};

Navbar.propTypes = {
	advanced: PropTypes.bool,
	size: PropTypes.oneOf(SIZES),
	className: PropTypes.string,
	children: PropTypes.node,
};

Navbar.defaultProps = {
	advanced: false,
};

export default Navbar;